$primary-color: #2d2d2d;
$secondary-color: #030e19;
$highlight-color: #1890ff;

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: $secondary-color;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
  //text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  //font-weight: bold;
  font-size: 18px;
}

.fw-darkbackground {
  background-color: $primary-color;
}

.ant-layout{
  background-color: $primary-color;
  color: #d9d9d9;
}

.ant-layout-footer{
  background-color: $secondary-color;
}

.ant-drawer-wrapper-body{
  background-color: $secondary-color;
}

.ant-progress-inner{
  vertical-align: top;
}

.ant-modal-content{
  background-color: rgba(0, 0, 0, 0);
  box-shadow: 4px 4px 5px 0 rgba(0, 0, 0, 0.5);
}

.ant-modal-confirm-title{
  color:white;
}

.ant-modal-close{
  color: white;
}

.ant-modal-confirm-body > .ant-modal-confirm-title{
  color:white;
}

.ant-modal-confirm-body > .ant-modal-confirm-content{
  color: white;
}

.ant-modal-body{
  background-color: $primary-color;
}

.ant-modal-footer{
  background-color: $primary-color;
  border-top: 1px solid #717171;
}

.ant-menu-sub.ant-menu-inline{
  padding-bottom: 10px;
}
// .ant-progress-show-info .ant-progress-outer{
//   margin-right: calc(-2em - 14px);
//   padding-right: calc(2em + 14px);
// }
.ant-progress-line{
  font-size: 12px;
}
.ant-progress-text{
  vertical-align: top;
  color: whitesmoke;
  //font-size: 12px;
}

.ant-menu-dark, .ant-menu-dark .ant-menu-sub{
  background: inherit;
}

.ant-tree{
  color: whitesmoke;
}

.ant-tree li .ant-tree-node-content-wrapper{
  color: whitesmoke;
}

.my-ant-modal{
  top:15px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5;
  list-style: none;
  -webkit-font-feature-settings: 'tnum';
  font-feature-settings: 'tnum', "tnum";
  position: relative;
  width: 620px;
  max-width: 100vw;
  margin: 0 auto;
  padding-bottom: 24px;
  pointer-events: none;
}

.my-ant-modal-header{
  background-color: #001529;
  border-bottom: 1px solid #717171;
  padding: 16px 24px;
  color: rgba(0, 0, 0, 0.65);
  border-radius: 4px 4px 0 0;
}

.my-ant-modal-body{
  color:white;
  background-color: $primary-color;
  padding: 24px;
  font-size: 14px;
  line-height: 1.5;
  word-wrap: break-word;
}

.my-ant-modal-footer{
  background-color: $primary-color;
  border-top:1px solid #717171;
  padding: 10px 16px;
  text-align: right;
  border-radius: 0 0 4px 4px;
}

.my-ant-modal-title{
  color:rgba(255, 255, 255, 0.86);
  margin: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  word-wrap: break-word;
}

.my-editor-label{
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
}

.my-editor-button-label{
  font-weight: 600;
}

.overlay {
  /* Height & width depends on how you want to reveal the overlay (see JS below) */   
  height: 100%;
  width: 100%;
  position: fixed; /* Stay in place */
  left: 0;
  top: 0;
  overflow-x: hidden; /* Disable horizontal scroll */
}

.overlay-reader{
  background-color: rgb(0,0,0); /* Black fallback color */
  background-color: rgba(0,0,0, 0.9); /* Black w/opacity */
  transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* Position the content inside the overlay */
.overlay-content {
  position: relative;
  top: 0%; /* 25% from the top */
  width: 100%; /* 100% width */
  text-align: center; /* Centered text/links */
  //margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */
}

.overlay-button{
  position: relative;
  //border: 1px solid red;
  box-sizing: border-box;
}

.mobile-header{
  position: fixed;
  z-index: 1;
  width: 100%;
  //height: 40px;
  background-color: #001529;
}

.hamburger-container{
  display: flex;
  justify-content: right;
  align-items: center;
  height: 40px;
  font-size: 36px;
}

.hamburger{
  margin-right: 6.5px;
  margin-left: auto;
  color: $highlight-color;
  font-size: 32px;
}

.headerbutton-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}

.with-shadow {
  text-shadow:
  -1px -1px 0 #000,
  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000;  ; /* FF3.5+, Opera 9+, Saf1+, Chrome, IE10 */
}

.comparison-table tr{
  border-bottom: 1px solid gray;
}

.album-flag{
  position: absolute;
  height: 40px;
  width: 100%;
  //background-color: rgba(255, 105, 46, 0.3);
  top: 30px;
  background-image: linear-gradient(45deg, #6303b1af, #ff0099a2);
	transform: skewY(-22.5deg);
}

h3{
  font-family: sans-serif;
  color:white;
  margin-bottom: 0px;
}